<template>
  <div>
    <button id="btn" @click='btn'>del</button>
    <template v-if="change==0">
        <div  class="chat-wrapper">
            <div class="chat-container" >
                <div v-if="image==0" class="chat-img">
                  <img src="../assets/chatbox2.png"  alt="" @click="change+=1">
                  <div class="close" @click="image+=1"><i class="fa fa-times" aria-hidden="true"></i></div>
                </div>  
                <div class="chat-message"  @click="change+=1">
                  <div class="chat-test">Chat vi shop nh !</div>
                  <div class="chat-notification"><div class="noti">1</div></div>
                </div>
              </div>
        </div>
    </template>
    <template v-else>
        <div  class=" chatbox-wrapper">
            <div class="chatbox-container">
              <div class="chat-header">
                  <h4>H Tr Khch Hng</h4>
                  <div class="nv">
                    <img src="../assets/custom2.png" alt="custom">
                    <div class="info">
                      <div class="info-message">Nhn vin  sn sng h tr bn</div>
                      <p>Nhn vin</p>
                      <p>Chat ngay vi shop</p>
                    </div>
                  </div>
                  <div class="chat-change" @click="change-=1" ><i class="fa fa-times" aria-hidden="true"></i></div>
              </div>
              <div v-if="sub==null" class="push-notification" @click='main'>
                  <div class="mess-notification">Bn cha ng k nhn thng bo trn trnh duyt khi bn c tinh nhn mi</div>
                  <span class="get-notification" @click="sub=1"  >Nhn thng bo</span>
                </div>
              <div class="chatbox-feed" v-on:click="change+=1">
                <ul ref="feed"> 
                  <li v-for="msg of messages " v-bind:key="msg.id"  :class="`${msg.from===hostname ? 'received' : 'sent' }`" >
                    <div class="msg-wrapper">
                      <div v-if="msg.from===hostname" class="admin"><i class="fa fa-user" aria-hidden="true"></i></div>
                      <div class="text">{{msg.message}}</div>
                    </div>
                  </li>
                  <li v-if="isTyping" class="received">
                      <div class="msg-wrapper">
                          <div class="admin"><i class="fa fa-user" aria-hidden="true"></i></div>
                          <div class="typing-indicator">
                            <span></span>
                            <span></span>
                            <span></span>
                          </div>
                        </div>
                  </li>              
                </ul>
              </div>
              <div  class="textarea-container">
                  <textarea v-model="message"  @keydown="typing" placeholder="G vo y v nhn enter..."  @keydown.enter="send"></textarea>
                  <div class="paper-send"><i class="fa fa-paper-plane" aria-hidden="true" @click="send" ></i></div>
              </div>
            </div>
        </div>
    </template>
    <div v-if="change==0" class="mobile-chat-wrapper">
        <div class="mobile-chat" @click="change+=1" >
          <i class="fa fa-comment" aria-hidden="true"></i>
          <div class="mobile-text">Chat ngay</div>
        </div>
      </div>
  </div>
</template>

<script>
import io from 'socket.io-client';
import {main,btn} from '../js/push_notification'
export default {
  
  data:function() { return  {
    hostname:window.location.hostname,
    sub:localStorage.getItem('sub'),
    isResponse:false,
    cc:null,
    isTyping:false,
    typingClock:null,
    isMoblie:false  ,
    image:0,
    messages :[],
    message:'',
    change:0,
    socket: io.connect('http://localhost:4000/client')
  }},
  beforeDestroy () {
    if (typeof window !== 'undefined') {
      window.removeEventListener('resize', this.onResize)
    }
  },
  created() {
    window.addEventListener('resize', this.onResize)
  },
  methods: {
    main:main,
    btn:btn,
    send(e) {
      e.preventDefault();
      if(this.message!="") {
        this.socket.emit("oneClientSendMessage",this.message,window.location.hostname,(data)=> { 
            //this.messages.push(data);
        });
        if(this.isResponse==false) {
          this.socket.emit('sendNotification',window.location.hostname,this.message);
        }
        this.message='';
      }
    },
    scrollToBottom() {
      setTimeout(() => {
           if(this.$refs.feed) {
            this.$refs.feed.scrollTop = this.$refs.feed.scrollHeight;
           }
      },50)
   },
   onResize () {
    this.isMobile = window.innerWidth < 600
    },
    typing() {
      this.socket.emit('ClientTyping');
    }
  },
  mounted:function() {
    this.socket.emit('selectDb',window.location.hostname);
    this.socket.on('AdminTyping',()=> {
      this.isTyping=true;
      if(this.typingClock) clearTimeout(this.typingClock);
      this.typingClock =setTimeout(()=> {
        this.isTyping=false;
      },3000)
    })
    var id = localStorage.getItem('id');
    this.socket.emit("check",id,(id) => {
      localStorage.setItem('id',id);
    });
    this.socket.on('ll',(msg)=>{
    })
    this.socket.on('messageClientToAdmin',(msg)=>{

      this.messages.push(msg);
       this.scrollToBottom();
    })
    this.socket.on('messageAdminToClient',(msg) => {
      this.isResponse=true;
      clearTimeout(this.typingClock);
      this.isTyping=false;
      this.messages.push(msg);
      
    })
    this.socket.on('clientMessages',(msgs)=> {
      this.messages=msgs;
    })
  },
  updated() {
    this.scrollToBottom();
  }
}
</script>

<style scoped>
@import '../css/reboot.css';
@import '../css/font.css';

html {
  width: 100%;
  height: 100%;
}
.chat-wrapper {
  position: fixed;
  bottom: 0;
  right: 10px;
}
.chatbox-wrapper {
  position: fixed;
  bottom: 0;
  right: 10px;
}
.chat-img img {width: 100%;}
.chat-img {cursor: pointer;position: relative;}
.chat-img .close {position: absolute;top: 0;right: 0;cursor: pointer;z-index: 100;color:red;font-size: 15px;display: none;}
.chat-img:hover .close {display: block;position: absolute;top: 0;right: 0;}
.chat-message {cursor: pointer; background: #79b643;position: relative;border-radius: 8px;padding: 5px 15px; min-width: 300px}
.chat-notification {
  display: flex;
  justify-content: center;
  position: absolute;
  top: -9px;
  right: -6px;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: red;
}
.noti {
  color: white;
  font-size: 13px;
  font-weight: bold;
}
.msg-feed {
  height: 50px;
  flex: 8;
  padding-top: 10px;
  overflow-y: scroll;
}

.conversation {
  width: 30%;

}

.conversation.client {
  border: 1px solid black;
  height: 400px;
}

.conversation h4 {margin-top: 10px;font-size: 17px;border-bottom: 1px solid #808080bf;padding-left: 10px;padding-bottom: 10px;}

.conversation textarea {
  width: 100%;
  margin-bottom: 4px;
  height: 100px;
  color: black;
  resize: none;
  border: 1px solid #80808061;
  margin-bottom: -1px;
}
.chatbox-feed {
  height: 74.5%;
  min-width: 350px;
  min-height: 350px;
}
.chatbox-container {
  border: 2px solid #8080802b;
  border-radius: 6px;
  height: 100%;
}
.chat-header {
  background: #79b643;
  padding: 20px 10px 14px 20px;
  font-weight: bold;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
}

.nv {
    display: flex;
    padding: 0;
    font-size: 11px;
    padding-bottom: 1px;
}

.info-message {
    color: white;
    font-size: 13px;
    padding-bottom: 2px;
}

.info p {
    margin: 0;
    color: #ffffffc7;
}

.info {margin-top: 5px;}

.nv img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin-right: 10px;
    margin-top: -2px;
}
.chat-header h4 {
  font-size: 13px;
  font-weight: 400;
  color: white;
  text-align: center;
  display: none;
}
.chatbox-feed ul li {
  margin-top:2px;
}
li.received {
  margin-bottom: 1px;
  padding-left: 7px;
  margin-bottom: 5px;
}
.chatbox-feed ul {
  height: 350px;
  margin-top: 5px;
  padding: 0;
  list-style: none;
  overflow-y: auto;
}

.admin {
    background: white;
    position: relative;
    border-radius: 50%;
    justify-content: center;
    font-size: 26px;
    color: #808080ba;
    display: inline-block;
    border: 1px solid #80808017;
    top: 5px;
}

.admin i {
    padding: 7px 10px;
    border-radius: 50%;
}
.text {
  text-align: left;
  display: inline-block;
  padding: 6px 10px 6px 13px;
  border-radius: 15px;
  color:white;
  max-width: 180px;
  font-size: 15px;
  overflow-wrap: break-word;
}
.sent .text {
  background: #79b643;
}
.received .text {
  background: #e9ebee;
  color:black;
}
.sent {
  text-align: right;
  margin-bottom: 1px;
  padding-right: 7px;
}
.textarea-container {
  position: relative;
  width: 100%;
  border-top: 2px solid #63626261;
}
.textarea-container textarea {
  width: 100%;
  border-bottom: none;
  resize: none;
  border-left: none;
  border-right: none;
  outline: none;
  border-top: none;
}
textarea::placeholder {
  padding:10px 0 0 10px;
  color: #0000005e;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif
}
textarea {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif
}
.chatbox-feed ul::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}

.chatbox-feed ul::-webkit-scrollbar
{ display: block;
	width: 12px;
	background-color: #F5F5F5;
}

.chatbox-feed ul::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: rgba(0,0,0,.3);
}
.chatbox-feed ul:hover::-webkit-scrollbar {
  display: block;
}
.chat-header {
  position: relative;
}

.chat-header {}
.chat-change  {
  position: absolute;
  top: 0;
  right: 5px;
  cursor: pointer;
  color: white;
  font-weight: lighter;
  font-size: 15px;
  padding: 5px;
}
.cc {
  background: green;
  width: 200px;
}
.paper-send {
  position: absolute;
  top: -2px;
  right: 9px;
  font-size: 35px;
  color: #79b643;
  display: none;
}
.mobile-chat {
  background: #79b643;
  padding: 5px 10px;
  border-radius: 10px;
}
.mobile-chat i {
  color:white;
  font-size: 20px;
  margin-right: 4px;
}
.mobile-text {
  display: inline;
  color: white;
}

.mobile-text {}
.mobile-chat-wrapper {
  display: none;
  position: fixed;
  bottom: 5px;
  right: 5px;
}
.typing-indicator {
  display: inline-block!important;
  background-color: #E6E7ED;
  width: auto;
  border-radius: 50px;
  top: 11px;
  padding: 11px 15px;
  margin: 0 auto;
  position: relative;
  -webkit-animation: 2s bulge infinite ease-out;
  animation: 2s bulge infinite ease-out;
}
.typing-indicator:before, .typing-indicator:after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: -4px;
  height: 0px;
  width: 0px;
  border-radius: 50%;
  background-color: #E6E7ED;
}
.typing-indicator:after {
  height: 0;
  width: 0;
  left: -10px;
  bottom: -10px;
}
.typing-indicator span {
  height: 6px;
  width: 6px;
  float: left;
  margin: 0 1px;
  background-color: #9E9EA1;
  display: block;
  border-radius: 50%;
  opacity: 0.4;
}
.typing-indicator span:nth-of-type(1) {
  -webkit-animation: 1s blink infinite 0.3333s;
          animation: 1s blink infinite 0.3333s;
}
.typing-indicator span:nth-of-type(2) {
  -webkit-animation: 1s blink infinite 0.6666s;
          animation: 1s blink infinite 0.6666s;
}
.typing-indicator span:nth-of-type(3) {
  -webkit-animation: 1s blink infinite 0.9999s;
          animation: 1s blink infinite 0.9999s;
}

@-webkit-keyframes blink {
  50% {
    opacity: 1;
  }
}

@keyframes blink {
  50% {
    opacity: 1;
  }
}
@-webkit-keyframes bulge {
  50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
}
@keyframes bulge {
  50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
}

.push-notification {
  width: 350px;
  background: #85cf2f;
  color: white;
  z-index: 100;
  font-size: 14px;
  padding: 2px 0 6px 5px;
  position: absolute;
}

span.get-notification {
    margin-left: 5px;
    background: #FF9800;
    border-radius: 10px;
    padding: 1px 7px;
    cursor: pointer;
}

.mess-notification {
    display: inline;
}

@media screen and (max-width: 480px) {
  .chatbox-wrapper {
     width: 100%;
     height: 100%;
     padding-left: 0;
     right: 0;
  }
  .chatbox-container {
    border:none;
    border-radius: 0;
  }
  .chat-header {
    padding: 24px 20px;
    border-radius: 0;
  }
  .textarea-container{
  }
  .textarea-container textarea {
  width: 90%;
  }
  .chatbox-feed ul {
    height: 100%;
  }
  textarea::placeholder {
    padding:12px 0 0 10px;
  }
  .paper-send {
    display: block;
  }
  .mobile-chat-wrapper {
    display: block;
  }
  .chat-wrapper {
    display: none;

  }
  .push-notification {
    width: 100%;
    /* DISPLAY: none; */
  }
}
</style>



